.referral-system-section {
    margin-bottom: 40px;
  }
  
  .referral-system-section .card {
    height: auto;
  }
  
  .referral-system__link-wrapper {
    display: flex;
    gap: 10px;
    align-items: center;
    background: var(--base-color);
    color: #000;
    padding: 15px;
    margin-bottom: 10px;
  }
  
  .referral-system__link {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 350px;
  }
  
  @media (max-width: 520px) {
    .referral-system__link {
      max-width: 230px;
    }
  }
  
  .referral-system__link-copy:hover {
    cursor: pointer;
  }
  
  .referral-system__copied {
    padding: 10px;
    background: rgba(2, 70, 29, 0.1);
  }