.social-icon {
    width: 40px;
    height: 40px;
    background-color: #f1f3f5;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid var(--base-color);
    box-sizing: border-box;
  }
  
  .social-icon svg {
    width: 100%;
    height: 100%;
  }
  
  .social-icon--telegram {
    color: #29b6f6;
  }
  
  .social-icon--twitter {
    background-color: #29b6f6;
    color: #fff;
    padding: 7px;
  }
  
  .social-icon--discord {
    padding: 4px;
    color: #fff;
    background-color: #5865f2;
  }
  
  .social-icon--docs {
    padding: 7px;
  }