.hive-card {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    height: 100%;
    color: #fff;
    background: #000;
    position: relative;
    margin-bottom: 40px;
  }
  
  .hive-card__content {
    padding: 35px;
    position: relative;
    z-index: 1;
  }
  
  .hive-card::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 10px solid var(--base-color);
    z-index: 0;
  }
  
  .hive-card::after {
    content: "";
    position: absolute;
    width: 100%;
    left: 0;
    bottom: -90px;
    height: 110px;
    background-image: url("../../images/honey-smudges.svg");
    background-repeat: repeat-x;
    z-index: -1;
  }
  
  .hive-card__title {
    font-size: 34px;
    margin-bottom: 30px;
    color: var(--base-color);
    font-family: "Jolly Lodger", cursive;
  }