.header {
    padding: 10px 0;
    width: 100%;
    margin-bottom: 70px;
    background-color: var(--base-color);
    position: relative;
  }
  
  .header::before {
    content: "";
    position: absolute;
    width: 100%;
    bottom: -110px;
    height: 110px;
    background-image: url("../../images/honey-smudges.svg");
    background-repeat: repeat-x;
    z-index: -1;
  }
  
  .header__content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;
    z-index: 1;
  }
  
  .header__content-buttons {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
  }